/* Estilos para el navbar */
.inicio-navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #FFFFFF;
    padding: 10px 0; /* Eliminamos el relleno interno horizontal */
    width: 100%; /* Aseguramos que el ancho sea del 100% */
    margin: 0;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Agregamos una sombra con los valores adecuados */
  }
  
  
  .navbar-left ul {
    list-style-type: none;
    padding: 0;
    display: flex;
  }
  
  .navbar-left ul li {
    margin-right: 20px;
    cursor: pointer;
  }
  
  .navbar-right {
    display: flex;
    align-items: center;
  }
  
  .user-image {
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
  
  /* Estilos para la tabla */
  /* Estilos para la tabla */
  .table-container {
    margin-top: 120px;
    display: flex;
    justify-content: center;
  }
  
  table {
    width: 80%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  
  
  table.reserva {
    margin-top: 3%;
    margin-left: auto;
    margin-right: auto;
    width: 100%;  /* Establece el ancho al 100% para que sea responsive */
    max-width: 800px;  /* Opcional: establece un ancho máximo para evitar que la tabla se vuelva demasiado ancha en pantallas grandes */
    border-collapse: collapse;  /* Combina los bordes de las celdas para un aspecto más limpio */
}

th, td {
    padding: 10px;
    border: 1px solid #ddd;  /* Añade bordes a las celdas para mayor claridad */
}

@media (max-width: 600px) {
    /* Estilos específicos para pantallas más pequeñas */
    table.reserva {
        width: 100%;
    }
    
    th, td {
        display: block;  /* Cambia a un formato de bloque en pantallas más pequeñas */
        width: 100%;  /* Ocupa todo el ancho disponible */
        box-sizing: border-box;  /* Incluye el relleno y el borde en el ancho total */
    }
}

  th {
    background-color: #f2f2f2;
  }
  
  tr:nth-child(even) {
    background-color: #ffffff;
  }
  
  tr:hover {
    background-color: #e2e2e2;
  }
  
  button {
    padding: 6px 10px;
    margin-right: 5px;
    cursor: pointer;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 4px;
  
  }
  
  button:hover {
    background-color: #4399FF;
  }
  
  button:active {
    background-color: #4399FF;
  }
  
  /* Estilos para el modal */
  .modal {
    display: block; /* Asegúrate de que el modal esté visible */
    position: fixed;
    left: 0;
    top: 0;
    margin-bottom: 20%;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.4); /* Fondo oscuro semi-transparente */
  }
  
  .modal-content {
    background-color: #fefefe;
    margin: -9% auto; /* Ubicación central vertical y horizontal */
    padding: 100px;
    border: 1px solid #ffffff;
    width: 90%; /* Ancho del modal */
  }
  
  .close {
    color: #fcf9f9;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }
  
  
  
  
  .btnr {
    position: absolute;
    
    right:340px; /* You can adjust this value to position the button exactly where you need it */
    height:30px;
    background-color: #0070FF;
    color: rgb(255, 255, 255);
    width: 160px;
    
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  
  input {
    width: 100%; /* Ocupa todo el ancho del contenedor */
    box-sizing: border-box; /* Incluye el padding y el borde en el ancho total */
    margin-bottom: 15px; /* Añade espacio entre los campos de entrada */
    height: 40px; /* Ajusta la altura según tus preferencias */
    padding: 10px; /* Añade relleno para separar el texto del borde */
    border-radius: 5px;
    border: 2px solid #ACA3B9;
  }
  
